"use client";

import Image from "next/image";
import Link from "next/link";
import { Heart, MapPin, Star } from "lucide-react";
import { useState } from "react";

interface PropertyCardProps {
  id: string;
  title: string;
  category: string;
  price: number;
  location: string;
  features: string[];
  rating: number;
  reviewCount: number;
  imageUrl: string;
  href: string;
}

const PropertyCard = ({
  id,
  title,
  category,
  price,
  location,
  features,
  rating,
  reviewCount,
  imageUrl,
  href,
}: PropertyCardProps) => {
  const [isSaved, setIsSaved] = useState(false);

  return (
    <Link
      href={href}
      className="group block bg-white rounded-xl shadow-md hover:shadow-xl transition-all duration-300 overflow-hidden"
    >
      {/* Image */}
      <div className="relative h-48 w-full overflow-hidden">
        <Image
          src={imageUrl}
          alt={title}
          fill
          className="object-cover group-hover:scale-110 transition-transform duration-300"
        />

        {/* Category Badge */}
        <div className="absolute top-3 left-3">
          <span className="bg-blue-600 text-white text-xs font-semibold px-3 py-1 rounded-full">
            {category}
          </span>
        </div>

        {/* Save Button */}
        <button
          onClick={(e) => {
            e.preventDefault();
            setIsSaved(!isSaved);
          }}
          className="absolute top-3 right-3 p-2 bg-white rounded-full hover:bg-gray-100 transition"
        >
          <Heart
            className={`w-5 h-5 ${
              isSaved ? "fill-red-500 text-red-500" : "text-gray-700"
            }`}
          />
        </button>
      </div>

      {/* Content */}
      <div className="p-4">
        {/* Title */}
        <h3 className="text-lg font-semibold text-gray-900 mb-2 line-clamp-1 group-hover:text-blue-600 transition">
          {title}
        </h3>

        {/* Location */}
        <div className="flex items-center gap-1 text-sm text-gray-600 mb-3">
          <MapPin className="w-4 h-4" />
          <span>{location}</span>
        </div>

        {/* Features */}
        <div className="flex items-center gap-2 text-sm text-gray-600 mb-3 flex-wrap">
          {features.map((feature, index) => (
            <span key={index}>
              {feature}
              {index < features.length - 1 && " •"}
            </span>
          ))}
        </div>

        {/* Price & Rating */}
        <div className="flex items-center justify-between pt-3 border-t border-gray-200">
          <div>
            <span className="text-xl font-bold text-gray-900">
              ৳{price.toLocaleString()}
            </span>
            <span className="text-sm text-gray-500">/month</span>
          </div>

          <div className="flex items-center gap-1">
            <Star className="w-4 h-4 fill-yellow-400 text-yellow-400" />
            <span className="text-sm font-semibold text-gray-900">
              {rating}
            </span>
            <span className="text-sm text-gray-500">({reviewCount})</span>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default PropertyCard;
