"use client";

import Image from "next/image";
import Link from "next/link";

interface InlineAdProps {
  title: string;
  subtitle: string;
  imageUrl: string;
  ctaText: string;
  ctaLink: string;
  backgroundColor?: string;
}

const InlineAd = ({
  title,
  subtitle,
  imageUrl,
  ctaLink,
  ctaText,
  backgroundColor = "bg-gradient-to-br from-orange-500 to-pink-500",
}: InlineAdProps) => {
  return (
    <Link
      href={ctaLink}
      className={`group block ${backgroundColor} rounded-xl shadow-md hover:shadow-xl transition-all duration-300 overflow-hidden h-full`}
    >
      {/* Image */}
      <div className="relative h-48 w-full overflow-hidden">
        <Image
          src={imageUrl}
          alt={title}
          fill
          className="object-cover group-hover:scale-110 transition-transform duration-300"
        />
      </div>

      {/* Content */}
      <div className="p-6 text-white">
        <h3 className="text-2xl font-bold mb-2">{title}</h3>
        <p className="text-sm mb-4 opacity-90">{subtitle}</p>

        <div className="inline-block bg-white text-gray-900 font-semibold px-6 py-2 rounded-lg hover:bg-gray-100 transition">
          {ctaText}
        </div>
      </div>
    </Link>
  );
};

export default InlineAd;
