"use client";

import Image from "next/image";
import Link from "next/link";

interface CardAdProps {
  title: string;
  subtitle?: string;
  imageUrl: string;
  ctaText: string;
  ctaLink: string;
  backgroundColor?: string;
}

const CardAd = ({
  title,
  subtitle,
  imageUrl,
  ctaText,
  ctaLink,
  backgroundColor = "bg-gradient-to-br from-blue-500 to-blue-600",
}: CardAdProps) => {
  return (
    <Link
      href={ctaLink}
      className="group block bg-white rounded-xl shadow-md hover:shadow-xl transition-all duration-300 overflow-hidden h-full"
    >
      {/* Image */}
      <div className="relative h-48 w-full overflow-hidden">
        <Image
          src={imageUrl}
          alt={title}
          fill
          className="object-cover"
        />
        {/* Overlay */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent"></div>
      </div>

      {/* Content */}
      <div className={`p-6 ${backgroundColor} text-white flex flex-col justify-between min-h-[200px]`}>
        <div>
          <h3 className="text-xl font-bold mb-2 leading-tight">
            {title}
          </h3>
          {subtitle && (
            <p className="text-sm opacity-90 mb-4">
              {subtitle}
            </p>
          )}
        </div>

        <button className="w-full bg-white text-blue-600 font-bold py-3 px-4 rounded-lg hover:bg-gray-100 transition">
          {ctaText}
        </button>
      </div>
    </Link>
  );
};

export default CardAd;
